@use "00-base/configure" as *;

.ma__steps-unordered {

  @include clearfix;
  margin-bottom: 45px;

  .pre-content > &,
  .post-content > &,
  .main-content--full .page-content > & {

    @include ma-container('restricted');
  }

  .ma__rich-text {
    margin-bottom: 30px;
  }

  .ma__action-step {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}